<template>
  <div class="bloglist">
    <div class="tit">
      <div class="houtui"></div>博客列表
    </div>
    <div class="qianjin"></div>
    <hr />
    <div
      class="list"
      v-for="(item, index) in this.$store.state.bloglist"
      :key="index"
      :id="bind(item)"
      v-on:click="chooseblog($event)"
    >{{item.split('_')[1]}}</div>
  </div>
</template>
<script>
export default {
  name: "bloglist",
  methods: {
    chooseblog(e) {
      this.$http
        .get(`http://${window.location.hostname}:9090/api/md`, {
          params: { choose: `${e.target.id}.md` }
        })
        .then(response => {
          this.$store.commit("changeblogcontent", response.data);
        })
        .catch(function(error) {
          // 请求失败处理
          console.log(error);
        });
      if (window.innerWidth <= 600) {
        document.getElementsByClassName("bloglist")[0].style.left = "-300px";
        document.getElementsByClassName("content")[0].style.color = "#333333";
        document.getElementsByClassName('content')[0].style.display = 'none'
        setTimeout(function(){document.getElementsByClassName('content')[0].style.display = 'block'},10)
      }
      document.getElementsByClassName('content')[0].scrollTop = 0;
    },
    bind(e) {
      return e;
    }
  }
};
</script>
<style lang="less">
@main-color: #4caf50;
.bloglist {
  position: relative;
  text-align: start;
  padding: 20px;
  width: 260px;
  color: #222;
  overflow: auto;
  font-weight: bold;
  font-family: "微软雅黑";
  user-select: none;
  transition: all 0.5s;
}
.list {
  padding: 5px;
  margin: 10px 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #444;
}
.list:hover {
  color: @main-color;
}
.tit {
  display: block;
  text-align: center;
  margin-bottom: 15px;
  font-size: large;
}
.houtui {
  display: none;
}
.qianjin {
  display: none;
}
@media screen and (max-width: 600px) {
  .bloglist {
    display: block;
    width: auto;
    float: left;
    position: fixed;
    left: 0px;
    z-index: 99;
    padding: 10px;
    height: 100%;
  }
}
</style>