<template>
  <div class="blog">
    <bloglist></bloglist>
    <blogcontent></blogcontent>
  </div>
</template>
<script>
import bloglist from "./blog/bloglist";
import blogcontent from "./blog/blogcontent";
export default {
  name: "blog",
  data() {
    return {};
  },
  components: {
    bloglist,
    blogcontent
  },
  mounted: function() {
    //这个是钩子函数
    //如果cartView函数要执行，必须先执行钩子函数
    //这个钩子函数完成了对cratView函数的调用
    //应该注意的是，使用mounted 并不能保证钩子函数中的 this.$el 在 document 中。为此还应该引入    Vue.nextTick/vm.$nextTick
    this.$nextTick(function() {
      this.getbloglist();
    });
  },
  methods: {
    getbloglist() {
      this.$http({
        method: "get",
        url: `http://${window.location.hostname}:9090/api/bloglist`,
        params: {}
      }).then(response => {
        this.$store.commit("setData", { key: "bloglist", val: response.data });
        if (this.$store.state.selectblog != "") {
          $(document.getElementById(this.$store.state.selectblog)).ready(
            document.getElementById(this.$store.state.selectblog).click()
          );
        }
      });
    },
    
  }
};
</script>
<style lang="less">
@main-color: #4caf50;
.blog {
  display: flex;
}
@media screen and (max-width: 600px) {
  .blog{
    display: block;
    overflow-y:scroll;
  }
}
</style>